
import backHome from '../components/backHome'
import wx from 'weixin-js-sdk'
import {mapGetters,mapActions}from 'vuex'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import empty from '../components/empty'
export default {
name: "alreadyrealizedf",
data(){
return{
pageSize:10,
tips:'',
onFetching:false,
obj:null,
nu:2,
sy:'',
ey:''
}
},
filters:{
statusFls(value){
let s;
switch (value){
case 'WISH_WAIT_ACHIEVE':
s = '待实现'
break
case 'WISH_WAIT_PAY':
s= '待付款，已经生成订单'
break
case 'WISH_WAIT_ACHIEVE':
s= '待实现'
break
case 'WISH_HAS_ACHIEVE':
s= '已实现'
break
}
return s
},
},
mounted(){
document.title = '好友心愿'

if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

this.getList(0,'')
},
computed:{
...mapGetters({
getfrWishlt:'getmyWishlt'
})
},
methods:{
...mapActions(['friendsWish']),
goToIndex(){
wx.miniProgram.switchTab({
url: '/pages/home/index'
})
},
seeDetail(id){
wx.miniProgram.navigateTo({
url:'/pages/webview/index?path=wishDetail&wishId='+id+'&mine=1'
})

},
seeOrder(){
wx.miniProgram.navigateTo({
url:'/pages/webview/index?path=myOrder'
})
},
getList(n,x){

this.showLoading()
this.friendsWish({this:this,flag:x,data:{page:n,pageSize:this.pageSize,status:'WISH_HAS_ACHIEVE'},method:this.friendsWish})
},
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.sy - this.ey > 30){
this.bindScroll()
}
}  ,
jizw(){
this.onFetching = false
},
bindScroll(){
let no = this.getfrWishlt.list.length
let to = this.getfrWishlt.max
if(no == to){
this.tips='没有更多了'
this.onFetching = true
return
}
let dh = this.$refs.content.scrollTop
let h = this.$refs.content.clientHeight
let bh = this.$refs.content1.clientHeight
if(bh - dh < h ){

if(!this.onFetching){
this.onFetching = true
this.tips='正在加载'
this.getList(Math.ceil(no/this.pageSize),true)
}

}
},
test(e){
console.log(e)
e.stopPropagation()
},
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {
console.log(8)
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
this.onFetching = false
}, 2000)
}
}

},
components:{
LoadMore , Scroller , backHome ,empty
}
}



